const platform = uni.getSystemInfoSync().platform
export default {
	name: 'UniLoadMore',
	props: {
		status: {
			// more-loadingloading-loadingnoMore-
			type: String,
			default: 'more'
		},
		showIcon: {
			type: Boolean,
			default: true
		},
		iconType: {
			type: String,
			default: 'auto'
		},
		color: {
			type: String,
			default: '#777777'
		},
		contentText: {
			type: Object,
			default () {
				return {
					contentdown: '',
					contentrefresh: '...',
					contentnomore: ''
				}
			}
		}
	},
	data() {
		return {
			platform: platform
		}
	},
	methods: {
		onClick() {
			this.$emit('clickLoadMore', {
				detail: {
					status: this.status,
				}
			})
		}
	}
}
